<template>
  <!-- eslint-disable vue/no-v-html -->
  <div class="condition-builder-rule tw-flex" :class="classes">
    <div class="condition-builder-rule-input tw-flex-grow tw-py-2">

      <div class="tw-flex">

        <div>
          <div class="select">
            <select v-model="rule" class="">
              <option
                      v-for="rule in rules"
                      :key="rule.id"
                      :value="rule"
              >
                {{ rule.label }}
              </option>
            </select>
          </div>
        </div>

        <div>
          <!-- List of operands (optional) -->
          <div class="select" v-if="typeof rule.operands !== 'undefined'">
            <select
                    v-model="query.operand"
                    class=""
            >
              <option
                      v-for="operand in rule.operands"
                      :key="operand"
              >
                {{ operand }}
              </option>
            </select>
          </div>

          <!-- List of operators (e.g. =, !=, >, <) -->
          <div class="select" v-if="typeof rule.operators !== 'undefined' && rule.operators.length > 1">
            <select
                    v-if="typeof rule.operators !== 'undefined' && rule.operators.length > 1"
                    v-model="query.operator"
                    class=""
            >
              <option
                      v-for="operator in rule.operators"
                      :key="operator"
                      :value="operator"
              >
                {{ operator }}
              </option>
            </select>
          </div>

          <!-- Basic text input -->
          <input
                  v-if="rule.inputType === 'text'"
                  v-model="query.value"
                  class=""
                  type="text"
                  :placeholder="labels.textInputPlaceholder"
          >

          <!-- Basic number input -->
          <input
                  v-if="rule.inputType === 'number'"
                  v-model="query.value"
                  class=""
                  type="number"
          >

          <!-- Datepicker -->
          <input
                  v-if="rule.inputType === 'date'"
                  v-model="query.value"
                  class=""
                  type="date"
          >

          <!-- Custom component input -->
          <div
                  v-if="isCustomComponent"
                  class="conditionbuilder-custom-component-wrap"
          >
            <component
                    :is="rule.component"
                    :value="query.value"
                    @input="updateQuery"
            />
          </div>

          <!-- Checkbox input -->
          <template
                  v-if="rule.inputType === 'checkbox'"
          >
            <div
                    v-for="choice in rule.choices"
                    :key="choice.value"
                    class=""
            >
              <input
                      :id="'depth' + depth + '-' + rule.id + '-' + index + '-' + choice.value"
                      v-model="query.value"
                      type="checkbox"
                      :value="choice.value"
                      class=""
              >
              <label
                      class="form-check-label"
                      :for="'depth' + depth + '-' + rule.id + '-' + index + '-' + choice.value"
              >
                {{ choice.label }}
              </label>
            </div>
          </template>

          <!-- Radio input -->
          <template
                  v-if="rule.inputType === 'radio'"
          >
            <div
                    v-for="choice in rule.choices"
                    :key="choice.value"
                    class=""
            >
              <input
                      :id="'depth' + depth + '-' + rule.id + '-' + index + '-' + choice.value"
                      v-model="query.value"
                      :name="'depth' + depth + '-' + rule.id + '-' + index"
                      type="radio"
                      :value="choice.value"
                      class=""
              >
              <label
                      class=""
                      :for="'depth' + depth + '-' + rule.id + '-' + index + '-' + choice.value"
              >
                {{ choice.label }}
              </label>
            </div>
          </template>

          <!-- Select without groups -->
          <div class="select" v-if="rule.inputType === 'select' && !hasOptionGroups">
            <select
                    v-model="query.value"
                    class=""
                    :multiple="rule.type === 'multi-select'"
            >
              <option
                      v-for="option in selectOptions"
                      :key="option.value"
                      :value="option.value"
              >
                {{ option.label }}
              </option>
            </select>
          </div>

          <!-- Select with groups -->
          <div v-if="rule.inputType === 'select' && hasOptionGroups">
            <select

                    v-model="query.value"
                    class=""
                    :multiple="rule.type === 'multi-select'"
            >
              <optgroup
                      v-for="(option, option_key) in selectOptions"
                      :key="option_key"
                      :label="option_key"
              >
                <option
                        v-for="sub_option in option"
                        :key="sub_option.value"
                        :value="sub_option.value"
                >
                  {{ sub_option.label }}
                </option>
              </optgroup>
            </select>
          </div>
        </div>
      </div>

    </div>

    <div class="condition-builder-rule-delete">
      <a @click="remove" v-bind:title="labels.removeRule" role="button" href="#" class="delete icon tw-p-3"></a>
    </div>
  </div>
</template>

<script>
    import ConditionBuilderRule from '../../components/ConditionBuilderRule';

    export default {
        extends: ConditionBuilderRule,
        computed: {
            classes() {
                let depthClasses = [
                    'tw-border-craft-yellow-600',
                    'tw-border-craft-blue-600',
                    'tw-border-craft-purple-600',
                    'tw-border-craft-green-600',
                    'tw-border-craft-yellow-600',
                ];

                return depthClasses[this.depth - 1];
            }
        },
    }
</script>

<style lang="scss">
  .condition-builder-rule {
    @apply tw-my-2;
    @apply tw-border-l-4 tw-border-gray-300 tw-border-solid;

    .condition-builder-rule-input {
    }

    .condition-builder-rule-delete {
    }

    .condition-builder-rule-input-select {

    }
  }
</style>
