<template>
  <!-- eslint-disable vue/no-v-html -->
  <div class="condition-builder-group" :class="classes">

    <div>
      <div class="condition-builder-group-header">
        <div class="tw-flex-grow">
          <div class="select">
          <select class="condition-builder-match-type-select"
                  v-model="query.logicalOperator">
            <option v-for="label in labels.matchTypes"
                    :key="label.id"
                    :value="label.id">
              {{ label.label }}
            </option>
          </select>
          </div>
        </div>
        <div>
          <template v-if="depth > 1">
            <a @click="remove" v-bind:title="labels.removeGroup" role="button" href="#" class="delete icon tw-p-3"></a>
          </template>
        </div>
      </div>

      <condition-builder-children v-bind="$props"/>

      <div class="condition-builder-group-footer">
        <template v-if="depth < maxDepth">
          <select @change="onChangeAdd($event)"
                  class="add-child-select">
            <option selected value="">Add...</option>
            <option @click="addRule" value="addRule">{{labels.addRule}}</option>
            <option @click="addGroup" value="addGroup">{{labels.addGroup}}</option>
          </select>
        </template>

        <template v-if="depth == maxDepth">
          <button type="button"
                  class="add-rule-btn"
                  @click="addRule">
            {{labels.addRule}}
          </button>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
    import ConditionBuilderGroup from '../../components/ConditionBuilderGroup';
    import CraftCpRule from './CraftCpRule.vue';

    export default {
        name: "ConditionBuilderGroup",

        components: {
            // eslint-disable-next-line vue/no-unused-components
            'ConditionBuilderRule': CraftCpRule
        },
        computed: {
            classes() {
                let depthClasses = [
                    'tw-border-craft-yellow-600',
                    'tw-border-craft-blue-600',
                    'tw-border-craft-purple-600',
                    'tw-border-craft-green-600',
                    'tw-border-craft-yellow-600',
                ];

                return depthClasses[this.depth - 1];
            }
        },

        extends: ConditionBuilderGroup,
    }
</script>

